import './../App.css';
import React, {useEffect, useState } from 'react';
//import { useDispatch, useSelector} from "react-redux";
import {useParams} from "react-router-dom";
import Remon from '@remotemonster/sdk';
//import {addParticipants, removeParticipants} from '../store';

function Conference (){
    
    //let participantsList = useSelector( (state) => state.participants);
    //let dispatch = useDispatch();

    let otherVideos;

    //roomName  
    let {roomid} = useParams();
    let myNum;
    let isConnected = false;
    let remon;
    let roomName = '';
    let currentRoom=[];
    const rooms = [];
    const key = "5c2b29b655cc314a665296dc32f9282d0710670f76cf8539d85d77e9df871c83";
    const serviceId = "39de11af-55f3-4abf-8457-fd57fddd62f6";
    
    // please register your own service key from remotemonster site.
    let config = {
        credential: {
            key: key,
            serviceId: serviceId,
            wsurl : "wss://signal.remotemonster.com/ws",
            resturl : "https://signal.remotemonster.com/rest",
        },
        view: {
            //remote: "#remoteVideo",
            local: "#myVideo"
        },
        media: {
            video: {
                width: { min: 320, max: 320 },
                height: { min: 320, max: 320 },
                frameRate: { min: 8, max: 30 },
                maxBandwidth: 3000,
                codec: 'H264'
            },
            audio: true
        },
        rtc:{
            audioType:"voice"
        }        
    };

   //   
   const videoAttrs = {
        class : "remote-video center w-320 h-240",
        autoPlay : true,
        muted : true,
        controls: true,
        playsInline: true,
        zIndex:1,
        background:'rgba(0, 0, 0, 0.5)',
        width:'80%'
    }
    
    //    
    const listener = {
        onInit(){
        },
        onConnect(chid) {
            console.log(`remon.listener.onConnect ${chid} at listener`);
        },
        onComplete() {
            console.log(`remon.listener.onComplete: ${remon.getChannelId()} `);
            currentRoom[remon.getChannelId()] = true;
        },
        onDisconnectChannel() {
            // is called when other peer hang up.
            remon.close();
            isConnected = false;
        },
        onClose() {
            // is called when remon.close() method is called.
            console.log(`remon.listener.onClose: ${remon.getChannelId()}`);
        },
        onError(error) {
            console.log(`remon.listener.onError: ${remon.getChannelId()} ${error}`);
        },
        onStat(result) {
            console.log(`EVENT FIRED: onStat:  ${JSON.stringify(result)}`);
        },
        //    ,   
        onRoomEvent(result){
            //join
            switch (result.event) {
                case 'join':
                    if(!currentRoom[result.channel.id]){
                        currentRoom[result.channel.id] = true;

                        let newVideo = document.createElement('video')
                        videoAttrs.id = result.channel.id.replace(":","-");
                        Object.keys(videoAttrs).forEach(key => newVideo.setAttribute(key, videoAttrs[key]))

                        config.view.remote = `#${newVideo.id}`
                        //config.view.local  = `#${newVideo.id}`
                          
                        // if(index == 0)
                        // {
                        //     config.view.local = `#${newVideo.id}`
                        //     config.view.remote = `#myVideo`//`#${newVideo.id}`
                        // }
                        // else{
                        //     config.view.remote = `#${newVideo.id}`
                        // }

                        newVideo.remon = new Remon({ config })
                        
                        otherVideos.appendChild(newVideo)
                        newVideo.remon.joinCast(newVideo.id.replace("-",":"))
                    }
                break;
                case 'leave':
                    if(currentRoom[result.channel.id] && result.channel.id !== remon.getChannelId()){
                        let video = document.getElementById(result.channel.id.replace(":","-"));
                        //   
                        otherVideos.removeChild(video);
                        delete currentRoom[result.channel.id]
                    }
                break;
            }
            console.log(`EVENT FIRED: onRoomEvent channel Id : ${remon.getChannelId()}`)
            console.log(`EVENT FIRED: onRoomEvent: ${JSON.stringify(result)}`)
        }
    };

    useEffect(()=>{    
        otherVideos = document.getElementById("otherVideos");
        createDummyRemonForSearchLoop();   
        InitRoom();

        //clean up
        return()=>{
            listener.onRoomEvent('leave');
            listener.onDisconnectChannel();
            alert('You left this room');
        }
    },[])

    async function start() {
        //   
      if (isConnected) {
        isConnected = false;
        Object.keys(currentRoom).forEach(function(id){
          if( id !== remon.getChannelId()){
            let video = document.getElementById(id.replace(":","-"));
            if(video && video.remon){
              otherVideos.removeChild(video);
            }
          }
          delete currentRoom[id];
        })
        remon.close()
      } 
      //Room  =>   
      else {
        roomName = roomid 
          ? roomid
          : 'remon';
        isConnected = true;
    
        //   
        //Local view( )   
        //roomName =   ID
        //  4 ~  1,024   ( /  X)
        await remon.createRoom(roomName)
    
        //       
        //   
        let participants = await remon.fetchRooms(roomName);
        //     Remon   joinCast() 
        participants.forEach(async function(participant, index){
          if(!currentRoom[participant.id]){
                    currentRoom[participant.id] = true;
                    let newVideo = document.createElement('video');        
                    videoAttrs.id =  participant.id.replace(":","-");
                    Object.keys(videoAttrs).forEach(key => newVideo.setAttribute(key, videoAttrs[key]))
                    config.view.remote =`#${newVideo.id}`;
                    
                    // if(index == 0)
                    // {
                    //     config.view.local = `#${newVideo.id}`
                    //     config.view.remote = `#myVideo`//`#${newVideo.id}`
                    // }
                    // else{
                    //     config.view.remote = `#${newVideo.id}`
                    // }

                    //config.view.local = `#${newVideo.id}`
                    newVideo.remon = new Remon({ config })
    
                    otherVideos.appendChild(newVideo)
    
                    //  
                    await newVideo.remon.joinCast(newVideo.id.replace("-",":"));
                }                
        }) 
      }
    }    
    
    //Remon     
    function createDummyRemonForSearchLoop() {
      if (remon) remon.close();
      remon = new Remon({ config, listener });
    }
    
    function InitRoom(){
        start();
    };

    return(
        <div>
            <div className='container'>
                <div className='col'>
                <div className="col-xl-4 col-lg-5">
                <div className="card shadow mb-4">
                  <div className="card-header py-3 d-flex flex-row align-items-center justify-content-between">                  
                    <h6 className="m-0 font-weight-bold text-primary">
                      Other Videos
                    </h6>
                  </div>
                   <div className="card-body">
                    <main className="text-center">
                       <div className="col" style={{ width:'250px', height:'200px' }} id="otherVideos">                        
                       </div>
                    </main>
                  </div>
                </div>
              </div>
              <div className="col-xl-8 col-lg-7">
                <div className="card shadow mb-4">
                  <div className="card-header py-3 d-flex flex-row align-items-center justify-content-between">                  
                    <h6 className="m-0 font-weight-bold text-primary">
                      Local Video
                    </h6>
                  </div>
                  <div className="card-body">
                    <main className="text-center">
                      <video
                        id="myVideo"
                        className="remote-video center w-300 h-300"
                        autoPlay
                        muted
                        controls
                        playsInline
                        style = {{ zIndex:1, background:'rgba(0, 0, 0, 0.5)', witdh:'100%', height:"80%" }} >
                      </video>
                      <h6
                        id="waitingTv"
                        className="m-0 font-weight-bold text-primary"
                        style = {{ zIndex:3, position:'absolute', bottom:'55px',right:'45px', visibility:'hidden' }}>
                        waiting
                      </h6>                     
                    </main>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
    )
}

